.image-gallery-slide-wrapper .image-gallery-left-nav .image-gallery-svg  {
  opacity: 0.1;
}

.image-gallery-slide-wrapper .image-gallery-right-nav .image-gallery-svg  {
  opacity: 0.1;
}


.image-gallery-slide-wrapper .image-gallery-fullscreen-button .image-gallery-svg  {
  opacity: 0.1;
}
.image-gallery-slide-wrapper .image-gallery-play-button .image-gallery-svg  {
  opacity: 0;
}


.image-gallery-slide-wrapper:hover .image-gallery-left-nav .image-gallery-svg  {
  opacity: 1;
}

.image-gallery-slide-wrapper:hover .image-gallery-right-nav .image-gallery-svg  {
  opacity: 1;
}

.image-gallery-slide-wrapper:hover .image-gallery-fullscreen-button .image-gallery-svg  {
  opacity: 1;
}

.image-gallery-slide-wrapper:hover .image-gallery-play-button .image-gallery-svg  {
  opacity: 1;
}


