.box {
    display: block;
    position: fixed;
    top: 40px;
    left: 390px;
    max-width: 40vw;
    min-width: 22vw;
    max-height: 65vh;
    min-height: 10vh;
    margin: 40px;
    
    background-color: white; /* Needed for IEs */
    border: none;
    padding: 10px;
    opacity: 1;
    z-index: 99;

    -moz-box-shadow: 5px 10px 8px #888888;
    -webkit-box-shadow: 5px 10px 8px #888888;
    box-shadow: 5px 10px 8px #888888;
  
    
    zoom: 1;
  }

  .hiddenBox {
    display: block;
    position: fixed;
    top: 40px;
    left: 190px;
    width: 30vw;
    height: 40vh;
    margin: 40px;
    background-color: white; /* Needed for IEs */
    border: none;
    padding: 10px;
    opacity: 0;
    z-index: 99;

    -moz-box-shadow: 5px 10px 8px #888888;
    -webkit-box-shadow: 5px 10px 8px #888888;
    box-shadow: 5px 10px 8px #888888;
  
    
    zoom: 1;
  }
