@import url('https://fonts.googleapis.com/css?family=Parisienne');


.media-wrapper {        
    margin-top: 25px;
    margin-left: auto;
    margin-right: auto;
}

.grid-container {        
    display: block;
    margin-top: 25px;
    margin-left: 35px;
    
}
.alert-msg {
    display: block;
    font-size: 22px;
    font-weight: bold;
}
.title {
    font-family: "Parisienne";
    font-weight: 800;
    font-size: 66px;
    margin-top: -32px;
    background: -webkit-linear-gradient(rgb(11, 14, 14), rgba(20, 0, 0, 0.192));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}